<template>
<main>
    
    <Header :header="content"/>
    <h5 style="text-align:center;color:green;"> برای مثال کد 12345 وارد شود</h5>
    <div>
    <p>
        <input type="text" v-model="barcode" placeholder="کد دستگاه مورد نظر را وارد نمایید ...">
        <button @click="getDevice" class="btn btn-primary d-block mx-auto">دریافت</button>
    </p>
    </div>
    
    <h4 style="text-align:center;color:red;">{{error}}</h4>
    <table class="table  table-hover container">
            <tbody>
                <tr>
                <th>نام دستگاه</th>
                <td>{{devicename}}</td>
                </tr>

                <tr>
                <th>برند</th>
                <td>{{brand}}</td>
                </tr>

                <tr>
                <th>نوع دستگاه</th>
                <td>{{devicetype}}</td>
                </tr>

                <tr>
                <th>وضعیت دستگاه</th>
                <td>{{status}} <p style="display:inline;position:relative;right:10px"><button class="btn btn-success" style="font-size:0.8rem;" v-if="statuscode==200"  @click="putInfo">تغییر</button></p></td>
                
                </tr>

                <tr>
                <th>وضعیت فیزیکی</th>
                <td>{{health}}</td>
                </tr>

                <tr>
                <th>سیکل کاری</th>
                <td>{{cicle}}</td>
                </tr>

            </tbody>
    </table>
</main>
</template>

<script>
import axios from 'axios'
import Header from '../../components/Header.vue'
export default {
    name:'PartsView',
    components:{Header},
    data() {
        return {
            content:'اطلاعات دستگاه',
            devicename:'',
            brand:'',
            devicetype:'',
            status:'',
            error:'',
            health:'',
            barcode:'',
            cicle:'',
            statuscode:''
        }
    },
    methods: {
        getDevice(){
            if(this.barcode==='12345'){
            axios.get('../device.json')
            .then((response)=>{
                this.devicename=response.data.devicename;
                this.brand=response.data.brand;
                this.devicetype=response.data.devicetype;
                this.status=response.data.status;
                this.health=response.data.health;
                this.cicle=response.data.cicle;
                this.statuscode=response.status;
                this.barcode='';
                this.error='';
                })
            
            }else{
                this.error='کد قطعه اشتباه وارد شده است';
                this.barcode='';
            }
        },
        putInfo(){
            axios.put('../device.json',{

               status:"در حال کار" 
                
            })
        }

    },
}
</script>

<style lang="scss" scoped>
main{

    div{
        text-align: center;
            p{
                position: relative;
                display: inline-block;
                input{
                    
                    position:relative;
                    width: 30vw;
                    
                }
                button{
                    position: absolute;
                    left: 0;
                    top: 0;
                    bottom: 0;
                }

            }      

    }

    table{
        border-radius: 2px;
        background-color: rgb(255, 255, 255);
        width:80%;
        th{
            width: 15%;
        }
        td{
            text-align: center;
        }
    }
    
        

}
</style>